const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SEND_MESSAGE = 'SEND-MESSAGE';
const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT';

export const store = {
  _state: {
    dialogsPage: {
      contacts: [
        {id: 2, name: '–ê–Ω—è'},
        {id: 3, name: '–í–∞—Å—è'},
        {id: 4, name: '–ü–µ—Ç—è'},
        {id: 5, name: '–°–≤–µ—Ç–∞'},
        {id: 6, name: '–≠–ª—å–≤–∏—Ä–∞'},
      ],
      messages: [
        {id: 7, user: 6, message: '–ü—Ä–∏–≤–µ—Ç 7', date: new Date(2020, 0, 1, 13, 36)},
        {id: 6, user: 5, message: '–ü—Ä–∏–≤–µ—Ç 6', date: new Date(2020, 0, 1, 13, 34)},
        {id: 5, user: 4, message: '–ü—Ä–∏–≤–µ—Ç 5', date: new Date(2020, 0, 1, 13, 34)},
        {id: 4, user: 4, message: '–ü—Ä–∏–≤–µ—Ç 4', date: new Date(2020, 0, 1, 13, 33)},
        {id: 3, user: 3, message: '–ü—Ä–∏–≤–µ—Ç 3', date: new Date(2020, 0, 1, 13, 32)},
        {id: 2, user: 2, message: '–ü—Ä–∏–≤–µ—Ç 2', date: new Date(2020, 0, 1, 13, 31)},
        {id: 1, user: 2, message: '–ü—Ä–∏–≤–µ—Ç 1', date: new Date(2020, 0, 1, 13, 30)},
      ],
      newMessageText: '',
    },
    wallPage: {
      posts: [
        {id: 3, text: "–ó–∞–ø–∏—Å—å 1", likes: 100},
        {id: 2, text: "–ó–∞–ø–∏—Å—å 23", likes: 50},
        {id: 1, text: "–¢–µ–∫—Å—Ç –∑–∞–ø–∏—Å–∏ 100", likes: 5},
      ],
      newPostText: '–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ',
    }
  },

  getState () {
    return this._state;
  },


  subscribe (observer) {
    this.renderUI = observer;
  },

  renderUI () {
    console.error('No one is subscribed!')
  },

  dispatch (action) {
    switch (action.type) {
      case ADD_POST:
        this.addPost();
        break;
      case UPDATE_NEW_POST_TEXT:
        this.updateNewPostText(action.text)
        break;
      case SEND_MESSAGE:
        this.sendMessage();
        break;
      case UPDATE_NEW_MESSAGE_TEXT:
        this.updateNewMessageText(action.text)
        break;
      default:
        console.error(`Action type is incorrect: '${action.type}'!`)
        break
    }
  },

  addPost () {
    let id = this._state.wallPage.posts[0].id + 1
    let message = this._state.wallPage.newPostText
    if(message.trim() === ''){
      alert('–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ!')
      return
    }
    this._state.wallPage.posts.unshift({id: id, text: message, likes: 0})
    this._state.wallPage.newPostText = ''
    this.renderUI()
  },

  updateNewPostText (text) {
    this._state.wallPage.newPostText = text
    this.renderUI()
  },

  sendMessage () {
    let message = this._state.dialogsPage.newMessageText
    if(message.trim() === ''){
      alert('–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ!')
      return
    }
    let id = this._state.dialogsPage.messages[0].id + 1
    this._state.dialogsPage.messages.unshift({
      id: id,
      user: 1,
      message: message,
      date: new Date()
    })
    this._state.dialogsPage.newMessageText = ''
    this.renderUI()
  },

  updateNewMessageText (text) {
    this._state.dialogsPage.newMessageText = text
    this.renderUI()
  },

}

export const addPostActionCreator = () => ({type: ADD_POST})

export const updateNewPostTextActionCreator = (text) => ({
  type: UPDATE_NEW_POST_TEXT,
  text: text
})

export const sendMessageActionCreator = () => ({
  type: SEND_MESSAGE
})

export const updateNewMessageActionCreator = (text) => ({
  type: UPDATE_NEW_MESSAGE_TEXT,
  text: text
})